@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans JP", sans-serif;
  background-color: gainsboro;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none;
  -webkit-text-fill-color: #222222;
  -webkit-box-shadow: 0 0 0px 1000px rgba(0, 0, 0, 0) inset;
  transition: background-color 5000s ease-in-out 0s;
}

.wrapper {
  height: 100vh;
  background-color: gainsboro;
  display: flex;
  align-items: center;
  justify-content: center;
}
.wrapper .container {
  max-height: 625px;
  width: 425px;
  background-color: white;
  border-radius: 15px;
  padding: 5px 0;
}
.wrapper .container .user_wrapper .user_info {
  margin: 15px 25px;
  display: grid;
  align-items: center;
  grid-template-columns: auto auto 1fr;
  grid-template-areas: "go_back user_img user_name_state" "go_back user_img user_name_state";
}
.wrapper .container .user_wrapper .user_info .go_back {
  grid-area: go_back;
  text-decoration: none;
}
.wrapper .container .user_wrapper .user_info .go_back i {
  font-size: 1.5rem;
  color: #464646;
  margin-right: 20px;
}
.wrapper .container .user_wrapper .user_info .go_back:hover {
  opacity: 0.8;
  cursor: pointer;
}
.wrapper .container .user_wrapper .user_info img {
  grid-area: user_img;
  height: 50px;
  width: 50px;
  border-radius: 100%;
  object-fit: cover;
}
.wrapper .container .user_wrapper .user_info .user_name_state {
  grid-area: user_name_state;
  margin-left: 15px;
  display: grid;
  align-items: center;
  justify-content: start;
}
.wrapper .container .user_wrapper .user_info .user_name_state .name {
  font-size: 1rem;
  font-weight: 700;
  color: #464646;
}
.wrapper .container .user_wrapper .user_info .user_name_state .state {
  font-size: 0.85rem;
  color: #8d8d8d;
}
.wrapper .container .user_wrapper .user_info .user_logout {
  grid-area: user_logout;
  align-self: center;
  justify-self: end;
}
.wrapper .container .user_wrapper .user_info .user_logout button {
  background-color: #464646;
  color: #e8e8e8;
  border: none;
  padding: 8px 15px;
  border-radius: 5px;
  font-size: 1rem;
}
.wrapper .container .user_wrapper .user_info .user_logout button:hover {
  opacity: 0.9;
  cursor: pointer;
}
.wrapper .container .conversation_wrapper {
  height: 463px;
  overflow-y: auto;
  background-color: #f5f8fb;
}
.wrapper .container .conversation_wrapper::-webkit-scrollbar {
  width: 0.6rem;
}
.wrapper .container .conversation_wrapper::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px #2b2b2b10;
}
.wrapper .container .conversation_wrapper::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.316);
  outline: none;
  border-radius: 2.5px;
}
.wrapper .container .conversation_wrapper .conversation_container .message-received {
  justify-self: self-end;
}
.wrapper .container .conversation_wrapper .conversation_container .message-received .body {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.wrapper .container .conversation_wrapper .conversation_container .message-received .body span {
  background-color: #ffffff;
  text-align: end;
  margin: 15px 25px 0;
  padding: 10px 15px;
  max-width: 300px;
  color: #575757;
  border-radius: 1.25rem 1.25rem 1.25rem 0;
  overflow: hidden;
  -webkit-box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.1);
}
.wrapper .container .conversation_wrapper .conversation_container .message-received .author {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.wrapper .container .conversation_wrapper .conversation_container .message-received .author .time {
  text-align: start;
  margin: 5px 10px;
  color: #969696;
  margin: 5px 0px 5px 25px;
}
.wrapper .container .conversation_wrapper .conversation_container .message-sent {
  justify-self: self-end;
}
.wrapper .container .conversation_wrapper .conversation_container .message-sent .body {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.wrapper .container .conversation_wrapper .conversation_container .message-sent .body span {
  background-color: #1761ff;
  text-align: start;
  margin: 15px 25px 0;
  padding: 10px 15px;
  max-width: 300px;
  color: white;
  border-radius: 1.25rem 1.25rem 0 1.25rem;
  overflow: hidden;
  -webkit-box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.1);
}
.wrapper .container .conversation_wrapper .conversation_container .message-sent .author {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.wrapper .container .conversation_wrapper .conversation_container .message-sent .author .time {
  text-align: end;
  margin: 5px 10px;
  color: #808080;
  margin: 5px 25px 5px 0px;
}
.wrapper .container .send_message {
  margin: 15px 15px 0;
  padding-bottom: 10px;
}
.wrapper .container .send_message form {
  display: flex;
  align-items: center;
  justify-content: center;
}
.wrapper .container .send_message form input {
  padding: 16px;
  width: 75%;
  border: none;
  background-color: rgba(0, 0, 0, 0.1);
  font-size: 0.9rem;
  color: #575757;
  border-radius: 5px 0 0 5px;
}
.wrapper .container .send_message form input:focus {
  color: #575757;
  outline: none;
}
.wrapper .container .send_message form button {
  padding: 13.25px 15px;
  border: none;
  font-size: 0.8rem;
  font-weight: 600;
  color: #ebebeb;
  border-radius: 0 5px 5px 0;
  background-color: #262626;
}
.wrapper .container .send_message form button:hover {
  cursor: pointer;
  opacity: 0.9;
}
.wrapper .container .send_message form button i {
  font-size: 1.25rem;
}